@media screen and (max-width:1400px) {
    .dermo-left {
        margin-right: 0em;
    }
}

@media screen and (max-width:1200px) {

    .dermo-top img {
        max-width: 40em;
        max-height: 30em;
    }

    .dermo-footer p {
        margin-right: 2em;

    }

    .dermo-left {
        margin-right: 4em;
    }
}

@media screen and (max-width:710px) {
    .zlavy-images img {
        margin-right: 0em;
    }
}

@media screen and (max-width:665px) {

    .dermo-top img {
        max-width: 30em;
        max-height: 20em;
    }
}

@media screen and (max-width:500px) {
    .brands-title h2 {
        font-size: 1.5em;
    }

    .dermo-top img {
        max-width: 25em;
        max-height: 15em;
    }

    .dermo-footer {
        margin: 0 auto;
        align-items: center;

    }

    .dermo-footer p {
        max-width: 20em;
        min-width: 13em;
        width: 70%;
        margin: 1em auto;

    }

    .slide-up {
        transform: translateY(50%);
    }
}

@media screen and (max-width:425px) {

    .dermo-top img {
        max-width: 20em;
        max-height: 15em;
    }
}